import Typewriter from 'typewriter-effect'
import { S } from './Main_styles'

export const Main: React.FC = () => {
	return (
		<S.Main>
			<S.TitleWrapper>
				<S.Text>  </S.Text>
				<S.MainTitle>
					<Typewriter
						options={{
							strings: ['DELOTE BEAUTY'],
							autoStart: true,
							loop: true,
						}}
					/>
				</S.MainTitle>
				<S.Text> </S.Text>
			</S.TitleWrapper>
			<S.ScrollDown href='#about'> </S.ScrollDown>
		</S.Main>
	)
}
